import { useMutation } from '@tanstack/react-query'
import classNames from 'classnames'
import { useNavigate } from 'react-router-dom'
import { dogFoodApi } from '../../../../../api/DogFoodApi'
import { Loader } from '../../../../Loader/Loader'

export function ModalDeleteReviewInner({
  dataReview, closeDeleteModalHandler, productId, token,
}) {
  const navigate = useNavigate()
  console.log(dataReview)

  const reviewId = dataReview._id

  const {
    mutateAsync, isLoading, isError, error,
  } = useMutation({
    mutationFn: () => dogFoodApi.deleteReview(reviewId, productId, token),
  })

  if (isLoading) return <Loader />
  if (isError) return <p>{`${error} `}</p>

  const deleteItemHandler = async () => {
    await mutateAsync()
    closeDeleteModalHandler()
    navigate(`/products/${productId}`)
  }

  return (
    <>
      {' '}
      <p>
            ?
      </p>
      <div className="d-flex justify-content-center">
        <button
          onClick={closeDeleteModalHandler}
          className={classNames(
            'btn',
            'btn-primary',
            'btn-sm',
            'me-3',
            'mt-3',
          )}
          type="button"
        >
          
        </button>
        <button
          onClick={deleteItemHandler}
          className={classNames(
            'btn',
            'btn-danger',
            'btn-sm',
            'me-3',
            'mt-3',
          )}
          type="button"
        >
          
        </button>
      </div>
    </>
  )
}
