import classNames from 'classnames'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
// import { deleteItemFromCart } from '../../redux/slices/cartSlice'
import {
  changeItemIsChecked,
  deleteItemFromCart,
} from '../../redux/slices/cartSlice'
import { Counter } from '../Counter/Counter'
import { Modal } from '../Modal/Modal'
import styles from './cartListItem.module.css'

export function CartListItem({
  id,
  name,
  price,
  img,
  stock,
  description,
  count,
  isChecked,
  discount,
}) {
  const dispatch = useDispatch()

  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false)

  const closeDeleteModalHandler = () => {
    setIsDeleteModalOpen(false)
  }

  const openDeleteModalHandler = (e) => {
    e.preventDefault()
    setIsDeleteModalOpen(true)
  }

  // eslint-disable-next-line no-shadow
  const deleteItemHandler = (e) => {
    e.preventDefault()
    dispatch(deleteItemFromCart(id))
    closeDeleteModalHandler()
  }

  const changeStatusHandler = () => {
    dispatch(changeItemIsChecked(id))
  }

  const priceDiscount = Math.round(price * (1 - discount / 100))

  return (
    <div id={id} className={styles.card}>
      <div className={styles.card_name}>
        <input
          type="checkbox"
          checked={isChecked}
          onChange={changeStatusHandler}
        />
        <p>{name}</p>
      </div>
      <div className={styles.card_body}>
        <div className={styles.image__body}>
          <img src={img} className={styles.card__image} alt="" />
        </div>
        <div className={styles.description}>
          <p>{description}</p>
        </div>
        <div className={styles.stock}>
          <div>
            <p>
               :
              {' '}
              {stock}
            </p>
            <Counter id={id} stock={stock} count={count} />
          </div>
          <Link
            className={styles.link}
            onClick={openDeleteModalHandler}
            to="/#"
          >
            
          </Link>
        </div>
        {!discount ? (
          <p>
            {price}
            {' '}
            
          </p>
        ) : (
          <div>
            <p
              style={{
                color: 'red',
                fontSize: '14px',
                margin: 0,
                fontFamily: 'PT Sans,Helvetica,Arial,sans-serif',
                fontWeight: 700,
              }}
            >
              {priceDiscount}
              {' '}
              
            </p>
            <p style={{ fontSize: '12px', textDecoration: 'line-through' }}>
              {price}
              {' '}
              
            </p>
          </div>
        )}
      </div>
      <Modal isOpen={isDeleteModalOpen} closeHandler={closeDeleteModalHandler}>
        <p>
             
          {' '}
          <b>{name}</b>
          {' '}
           ?
        </p>
        <div className="d-flex justify-content-center">
          <button
            onClick={closeDeleteModalHandler}
            className={classNames(
              'btn',
              'btn-primary',
              'btn-sm',
              'me-3',
              'mt-3',
            )}
            type="button"
          >
            
          </button>
          <button
            onClick={deleteItemHandler}
            className={classNames(
              'btn',
              'btn-danger',
              'btn-sm',
              'me-3',
              'mt-3',
            )}
            type="button"
          >
            
          </button>
        </div>
      </Modal>
    </div>
  )
}
