/* eslint-disable jsx-a11y/label-has-associated-control */
import {
  Formik, Form, Field, ErrorMessage,
} from 'formik'
import classNames from 'classnames'
import { useMutation } from '@tanstack/react-query'
import { useNavigate } from 'react-router-dom'
import { addProductValidationSchema } from '../helpers/Validator'
import styles from './addProductForm.module.css'
import { dogFoodApi } from '../../../../api/DogFoodApi'
import { getIniteState } from '../../../../redux/initState'

const initialValues = {
  available: true, // boolean
  name: '', // string, 
  price: 0, // number, 
  pictures: '', // string
  discount: 0, // number
  stock: 0, // number
  wight: '', // string
  description: '', // string, 
}

export function AddProductForm() {
  console.log('  AddProductForm')
  const navigate = useNavigate()
  const { user: { token } } = getIniteState()

  const {
    mutateAsync, isLoading,
  } = useMutation({
    mutationFn: (values) => dogFoodApi.addProduct(values, token),
  })

  const submitHandler = async (values) => {
    console.log({ values })

    await mutateAsync(values)

    navigate('/products')
  }

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={addProductValidationSchema}
      onSubmit={submitHandler}
    >
      <Form className={styles.form}>
        <label htmlFor="name"> </label>
        <Field name="name" placeholder="  " type="text" />
        <ErrorMessage component="p" className="error" name="name" />

        <label htmlFor="price">, </label>
        <Field name="price" placeholder="" type="number" />
        <ErrorMessage component="p" className="error" name="price" />

        <label htmlFor="pictures"> </label>
        <Field name="pictures" placeholder="   .jpg" type="text" />
        <ErrorMessage component="p" className="error" name="pictures" />

        <label htmlFor="discount">, %</label>
        <Field name="discount" placeholder="  " type="number" />
        <ErrorMessage component="p" className="error" name="discount" />

        <label htmlFor="stock"> , </label>
        <Field name="stock" placeholder="   " type="number" />
        <ErrorMessage component="p" className="error" name="stock" />

        <label htmlFor="wight"> </label>
        <Field name="wight" placeholder="  : 100 " type="text" />
        <ErrorMessage component="p" className="error" name="wight" />

        <label htmlFor="description"> </label>
        <Field name="description" placeholder="  " type="text" />
        <ErrorMessage component="p" className="error" name="description" />

        <button
          disabled={isLoading}
          className={classNames(
            'btn',
            'btn-primary',
            styles.submitBtn,
          )}
          type="submit"
        >
           
        </button>
      </Form>
    </Formik>
  )
}
