import * as Yup from 'yup'

export const signUpFormValidationSchema = Yup.object({
  email: Yup.string()
    .email('  ')
    .required(' '),
  group: Yup.string()
    .max(10, '     10 ')
    .required(' '),
  password: Yup.string()
    .required('  ')
    .min(5, '   -      5 ')
    .matches(/[a-zA-Z]/, '     '),
})

export const signInFormValidationSchema = Yup.object({
  email: Yup.string()
    .email('  ')
    .required(' '),
  password: Yup.string()
    .required('  ')
    .min(5, '   -      5 ')
    .matches(/[a-zA-Z]/, '     '),
})

export const addProductValidationSchema = Yup.object({
  name: Yup.string()
    .max(80, '     80 ')
    .required(' '),
  price: Yup.number()
    .required(' '),
  pictures: Yup.string()
    .required(' '),
  discount: Yup.number()
    .required(' '),
  stock: Yup.number()
    .required(' '),
  wight: Yup.string()
    .max(8, '     8 ')
    .required(' '),
  description: Yup.string()
    .max(400, '     400 ')
    .required(' '),
})

export const addReviewValidationSchema = Yup.object({
  text: Yup.string()
    .max(400, '     400 ')
    .required(' '),
  rating: Yup.number()
    .required(' ')
    .integer()
    .max(5)
    .min(1),
})
